/* 最大宽度 */
@window-Width: 70%;

.page {
  padding: 32px;
  text-align: center;
}

/* 头部 */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  align-items: center;
  border-bottom: 1px solid #f8f8f8;
}

.sidebar-header .icon {
  color: #2f8cf0;
}

.sidebar-header-title {
  color: #515a6e;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.sidebar-header-more {
  font-size: 18px;
  color: #989898;
  cursor: pointer;

  &::after {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    border-top: 1px solid #D9D9D9;
    border-right: 1px solid #D9D9D9;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  &:hover {
    color: #2d8cf0;
    text-decoration: underline;
  }
}

.v-note-wrapper {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  background: #fff !important;
}

.v-note-wrapper .v-note-panel .v-note-edit.divarea-wrapper .content-input-wrapper {
  padding: 0 !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  padding: 0 !important;
}






















































































.header-wrap {
  width: 100%;
  height: 96px;
  display: flex;
  align-items: center;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .03);
  border-bottom: 1px solid #f0f0f0;
  background: #fff;
}

.header-inner {
  margin: 0 auto;
  width: @window-Width;
  display: flex;
  align-items: center;
}

.logo {
}

.logo-box {
  width: 168px;
  display: flex;
  align-items: center;

  & img {
    width: 100%;
  }
}

.nav {
  flex: 1;
}

.nav-box {
  display: flex;
  align-items: stretch;
  padding: 0 32px;
}

.nav-item {
  cursor: pointer;
  color: #17233d;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  padding: 0 32px;
  white-space: nowrap;

  &:hover {
    color: #2d8cf0;
  }
}

.nav-item-active {
  color: #2d8cf0;
}

@media screen and (min-width: 200px) and (max-width: 750px) {
  .search {
    display: none;
  }

  .header-inner {
    box-sizing: border-box;
    width: 100%;
    padding: 0 32px;
  }
}
